import './App.css';
import {useState,useEffect,useRef} from 'react';
import DiscreteSlider from './components/DiscreteSlider'
import useInterval from '@use-it/interval';
import W from 'lambert-w-function'
import Button from '@material-ui/core/Button';
import { Chart, registerables } from 'chart.js';
import React, { Component } from 'react'

function Tank({left,top,diameter,height}) {
  return (
    <>
    <div className="verticalLine"
      style={{left:left,top:top,height:height}}/>
    <div className="verticalLine"
      style={{left:`calc(${left} + ${diameter})`,top:top,height:height}}/>
    <div className="horizontalLine"
      style={{left:left,width:diameter,top:`calc(${top} + ${height})`}} />
    </>
  )
}

function Layout({leftA,topA,diameterA,heightA,heightDiff,length,diameterB,heightB,liquidHeightA,liquidHeightB,velocity,reynolds,frictionFactor,pipeDiameter}){
  function Exit(){
    return(
      <div className="verticalLine"
      style={{left:`calc(${leftA} + ${diameterA}/2)`,
              top:`calc(${topA} + ${heightA})`,
              height:`calc((${heightDiff}/2`}}
      />
    )
  }

  function Transmission(){
    return(
      <div className="horizontalLine"
      style={{left:`calc(${leftA} + ${diameterA}/2)`,
              top:`calc(${topA} + ${heightA} + ${heightDiff}/2)`,
              width: `calc(${length} - ${heightDiff})`}}
      />
    )
  }

  function Input(){
    return(
      <div className="verticalLine"
      style={{left:`calc(${leftA} + ${diameterA}/2 + ${length} - ${heightDiff})`,
              top:`calc(${topA} + ${heightA} + ${heightDiff}/2)`,
              height:`calc(${heightDiff}/2)`}}
      />
    )
  }

  function DrainingLiquid(){
    return(
      <div className="liquid"
      style={{left:leftA,
              top:`calc(${topA} + ${heightA} - ${liquidHeightA})`,
              height:liquidHeightA,
              width:diameterA
            }}
      />
    )
  }

  function FillingLiquid(){
    return(
      <div className="liquid"
      style={{left:`calc(${leftA} + ${diameterA}/2 + ${length} - ${heightDiff} - ${diameterB}/2)`,
              top:`calc(-${liquidHeightB} + ${heightB} + ${topA} +  ${heightA} + ${heightDiff})`,
              height:liquidHeightB,
              width:diameterB
            }}
      />
    )
  }

  return(
    <>
    <div id="volumeScore" style={{left:`calc(${leftA} + ${diameterA}/2)`,
    top:topA
    }}>
    V = {Math.round(parseFloat(diameterA)**2/4*parseFloat(liquidHeightA)*Math.PI)/1e6 + " m3"}
    </div>
    <div id="velocityScore" style={{left:`calc(${leftA} + ${diameterA}/2 + ${length}/2 - ${heightDiff}/2)`,
    top:`calc(${topA} + ${heightA} + ${heightDiff}/2 - 50px)`
    }}>
    v = {velocity.toFixed(3)} m/s
    <br/>
    q = {(velocity*Math.PI*pipeDiameter**2/4*1000).toFixed(3)} L/s
    <br/>
    Re = {reynolds.toFixed(0)}
    <br/>
    f = {frictionFactor.toFixed(5)}
    </div>
    <div id="volumeScore" style={{left:`calc(${leftA} + ${diameterA}/2 + ${length} - ${heightDiff})`,
    top:`calc(${topA} + ${heightA} + ${heightDiff} + ${heightB}/2)`
    }}>
    V = {Math.round(parseFloat(diameterB)**2/4*parseFloat(liquidHeightB)*Math.PI)/1e6 + " m3"}
    </div>
    <Tank left={leftA} top={topA} diameter={diameterA} height={heightA}/>
    <Exit/>
    <Transmission/>
    <Input/>
    <Tank left={`calc(${leftA} + ${diameterA}/2 + ${length} - ${heightDiff} - ${diameterB}/2)`}
          top={`calc(${topA} + ${heightA} + ${heightDiff})`}
          diameter={diameterB}
          height={heightB}
    />
    <DrainingLiquid/>
    <FillingLiquid/>
    </>
  )
}

function App() {
  Chart.register(...registerables);
  let [diameterA,setDiameterA] = useState(0);
  let [diameterB,setDiameterB] = useState(0);
  let [length,setLength] = useState(0);
  let [liquidHeightA,setLiquidHeightA] = useState(150);
  let [liquidHeightB,setLiquidHeightB] = useState(0);
  let [heightDiff,setHeightDiff] = useState(0);
  let [pipeDiameter,setPipeDiameter] = useState(0);
  let [count,setCount] = useState(0);
  let [time,setTime] = useState(0);
  const chartRef = React.createRef();
  let [newChart,setNewChart] = useState(React.createRef());
  let [myChartRef,setMyChartRef] = useState(0);
  let [finalVelocity,setFinalVelocity] = useState(0);
  let [finalFrictionFactor,setFinalFrictionFactor] = useState(0);
  let [finalReynolds,setFinalReynolds] = useState(0);

  useEffect(() => {
    myChartRef = chartRef.current.getContext("2d");
    setNewChart(new Chart(myChartRef,{
            type: 'scatter',
            options: {
              scales: {
                x: {
                  title: {
                    display: true,
                    text: 'Time (s)'
                  }
                },
                y: {
                  title: {
                    display: true,
                    text: 'Height (cm)'
                  }
                },
              }
            },
            data: {
              datasets: [{
                label: 'Height versus time',
                data: [],
                backgroundColor: 'rgb(255, 99, 132)'
              }],
            }
        }));
  },[])

  const pxToMeter = 1/100
  const viscosity = 1e-3;
  const density = 1e3;
  const gravity = 9.8;
  const dt = 5;
  let pipeDiameterMeters = pipeDiameter*pxToMeter;
  let diameterAMeters = diameterA*pxToMeter;
  let diameterBMeters = diameterB*pxToMeter;
  let liquidHeightAMeters = liquidHeightA*pxToMeter;
  let liquidHeightBMeters = liquidHeightB*pxToMeter;
  let heightDiffMeters = heightDiff*pxToMeter;
  let lengthMeters = length*pxToMeter
  let dv;
  function InitiateCount(){
    setCount(1);
  }

  function calcReynolds(v){
    return v*pipeDiameterMeters*density/viscosity
  }

  function calcFrictionFactor(re){
    if (re < 2100) {
      return 64/re
    } else {
      return 1/((-2*Math.log10(10**(-1/Math.log(10)*W(re*Math.log(10)/5.02))))**2)
    }
  }
  useInterval(() => {
    if (count > 0){
      let velocity = Math.sqrt(2*gravity*liquidHeightAMeters);
      let velocityNew = 2*velocity;
      dv = 1;
      while(Math.abs(dv) > 0.001){
        let reynolds = calcReynolds(velocity)
        let frictionFactor = calcFrictionFactor(reynolds)
        let headLoss = frictionFactor*velocity**2/(2*gravity)*lengthMeters/pipeDiameterMeters
        if (liquidHeightAMeters+heightDiffMeters>headLoss){
          velocityNew = Math.sqrt(2*gravity*(liquidHeightAMeters+heightDiffMeters-headLoss)/(1-(pipeDiameterMeters/diameterAMeters)**4))
        } else{
          velocityNew = velocity*0.75
        }
        dv = velocity - velocityNew;
        velocity = velocityNew
      }
      setTime(time+dt);
      setFinalVelocity(velocity);
      setFinalReynolds(calcReynolds(finalVelocity));
      setFinalFrictionFactor(calcFrictionFactor(finalReynolds));

      let dh = dt*velocity*(pipeDiameterMeters/diameterAMeters)**2
      liquidHeightAMeters -= dh;
      liquidHeightBMeters += dh*(diameterAMeters/diameterBMeters)**2;
      newChart.data.datasets[0].data.push({x:time,y:liquidHeightAMeters});
      newChart.update();
      setLiquidHeightA(liquidHeightAMeters/pxToMeter);
      setLiquidHeightB(liquidHeightBMeters/pxToMeter);
    }}, 100);

  return (
    <>
    <Button id="runButton" onClick={InitiateCount} variant="contained" color="primary">
          Run simulation
    </Button>
    <div id="sliderWrapper">
    <DiscreteSlider setSize={setDiameterA} size={diameterA} defaultValue="250" text="Draining tank diameter (cm)" min="50" max="500" step="10"/>
    <DiscreteSlider setSize={setDiameterB} size={diameterB} defaultValue="250" text="Filling tank diameter (cm)" min="50" max="500" step="10"/>
    <DiscreteSlider setSize={setLength} size={length} defaultValue="750" text="Pipe length (cm)" min="50" max="750" step="10"/>
    <DiscreteSlider setSize={setHeightDiff} size={heightDiff} defaultValue="200" text="Height difference (cm)" min="0" max="500" step="10"/>
    <DiscreteSlider setSize={setPipeDiameter} size={pipeDiameter} defaultValue="2.5" text="Pipe diameter (cm)" min="0.1" max="3" step="0.1"/>
    </div>
    <Layout leftA="350px" topA="100px"
            diameterA={diameterA+"px"} heightA="250px"
            diameterB={diameterB+"px"} heightB="200px"
            heightDiff={heightDiff+"px"} length={length+"px"}
            liquidHeightA={liquidHeightA+"px"}
            liquidHeightB={liquidHeightB+"px"}
            velocity={finalVelocity}
            frictionFactor={finalFrictionFactor}
            reynolds={finalReynolds}
            pipeDiameter={pipeDiameterMeters}
            />
    <canvas id="myChart" ref={chartRef}></canvas>
    </>
  );
};

export default App;
